/* Add styles for the modal overlay */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Adjust modal content width and styling */
  .modal-content {
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    width: 800px; /* Increase this value to make the modal wider */
    max-width: 90%; /* Ensure it doesn't exceed the screen width */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Close button */
  .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  
  /* Stepper */
  .stepper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .step {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .circle {
    width: 30px;
    height: 30px;
    background-color: #ccc;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    margin-bottom: 5px;
  }
  
  .step.active .circle {
    background-color: #4caf50; /* Active step color */
  }
  
  .step p {
    font-size: 14px;
    color: #333;
  }
  
  /* Modal action buttons */
  .modal-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .modal-actions button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 4px;
    background-color: #4caf50;
    color: white;
  }
  
  .modal-actions button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  