
.dashboard-container {
  padding: 10px;
  font-family: Arial, sans-serif;
  color: #333;
}

.dashboard-container .header {
  display: flex;
  justify-content: space-between; 
  align-items: center; 
}

.header {
  display: flex;
  justify-content: flex-start; 
  align-items: center;
  color: #18304A;
  margin-top: 10px; 
}
h1 {
  margin: 0;
  padding: 0;
}

.summary-cards {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
  margin: 20px 0;
  margin-top: 50px;
  justify-content: flex-start; 
}

.card1 {
  background-color: #B79344;
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: left;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 300px;
  height: 200px;
}

.card1 h3 {
  margin: 0;
  font-size: 20px;
}

.card2 {
  background-color: white;
  color: #18304A;
  padding: 20px;
  border-radius: 10px;
  text-align: left;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 300px;
  height: 200px;
}

.card2 h3 {
  margin: 0;
  font-size: 20px;
}

.card3 {
  background-color: white;
  color: #18304A;
  padding: 20px;
  border-radius: 10px;
  text-align: left;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 300px;
  height: 200px;
}

.card3 h3 {
  margin: 0;
  font-size: 20px;
}

.card4 {
  background-color: white;
  color: #18304A;
  padding: 20px;
  border-radius: 10px;
  text-align: left;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 300px;
  height: 200px;
}

.card4 h3 {
  margin: 0;
  font-size: 20px;
}

.card1-placeholder {
  width: 100%;
  padding: 10px;
  font-size: 50px;
  margin-top: 10px;
  color: white; 
  pointer-events: none;
}

.card2-placeholder {
  width: 100%;
  padding: 10px;
  font-size: 50px;
  margin-top: 10px;
  color: #18304A; 
  pointer-events: none;
}

.card3-placeholder {
  width: 100%;
  padding: 10px;
  font-size: 50px;
  margin-top: 10px;
  color: #18304A; 
  pointer-events: none;
}

.card4-placeholder {
  width: 100%;
  padding: 10px;
  font-size: 50px;
  margin-top: 10px;
  color: #18304A; 
  pointer-events: none;
}

.card p {
  margin: 5px 0;
  font-size: 24px;
  font-weight: bold;
}

.dashboard-content {
  display: grid;
  grid-template-columns: 2fr 3fr;
  gap: 40px;
  justify-content: flex-start; 
}


.chart_room {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  height: 400px;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.chart_room h3 {
  margin: 20px;
  font-size: 25px;
  color: #18304A;
}

.chart_revenue {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  width: 735px;
  height: 400px;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.chart_revenue h3 {
  margin: 20px;
  font-size: 25px;
  color: #18304A;
}

.tables {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-top: 20px;
  justify-content: flex-start;
  text-align: left;
}


.table {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

table {
  width: 100%;
  border-collapse: collapse;
}

.table-room-type {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.tables h3 { 
  margin: 20px;
  font-size: 20px;
  color: #18304A;
}

table th,
table td {
  text-align: left;
  padding: 8px;
  border: 2px solid #333;
}

.pie-chart-placeholder,
.line-chart-placeholder,
.bar-chart-placeholder {
  height: 300px;
  background-color: #e9ecef;
  border-radius: 8px;
  display: flex;
  justify-content: flex-start; 
  align-items: center;
  font-size: 14px;
  color: #888;
  padding-left: 20px; 
}


