.modal-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal-content {
  background: #fff;
  border-radius: 10px;
  padding: 20px;  /* Reduced padding */
  max-width: 600px;  /* Smaller width */
  max-height: 80%;   /* Limit max height */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: auto;
}

.modal-content h2 {
  margin-top: 30px;
  font-size: 24px;  /* Slightly smaller font size */
  color: #18304A;
  font-weight: 800;
}

.form-section h1 {
  font-size: 18px;  /* Slightly smaller font size */
  margin-bottom: 30px;
}

.form-section {
  margin-top: 30px;
  margin-bottom: 25px;
}

.form-section input,
.form-section select,
.form-section textarea {
  width: 100%;
  padding: 12px;  /* Reduced padding */
  border: 3px solid #ccc;
  border-radius: 25px;
  font-size: 12px;  /* Slightly smaller font size */
  color: #333;
  margin-bottom: 15px;
  margin-top: 5px;
  box-sizing: border-box;
  display: block;
  height: 45px;  /* Reduced height */
  background-color: #fff;
  transition: border-color 0.3s ease;
}

.styled-select {
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  appearance: none !important;
  background-color: #fff !important; 
  background: transparent url('data:image/svg+xml,%3Csvg%20xmlns=%22http://www.w3.org/2000/svg%22%20viewBox=%220%200%2024%2024%22%3E%3Cpath%20d=%22M7%2010l5%205%205-5z%22/%3E%3C/svg%3E') no-repeat right 15px center !important; /* Dropdown arrow */
  background-size: 1.5em !important;  
  padding-right: 50px !important; 
  padding: 20px !important; 
  font-size: 1.6rem !important;  
  height: 60px !important;  
  width: 100% !important;  
  border: 2px solid #ccc !important; 
  border-radius: 8px !important; 
  box-sizing: border-box !important; 
  transition: border-color 0.3s ease !important;  
}

.styled-select:focus {
  border-color: #18304A;  /* Change border color on focus */
}

.styled-select option {
  font-size: 0.7rem;  /* Larger font size for options */
  padding: 10px;  /* Increased padding for options */
}



.form-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 40px;  /* Reduced margin */
}

.form-buttons button {
  padding: 8px 18px;  /* Reduced padding */
  font-size: 16px;  /* Slightly smaller font size */
  border: none;
  border-radius: 25px;
  cursor: pointer;
}

.form-buttons button[type="submit"] {
  background: #18304A;
  color: #fff;
}

.form-buttons button[type="button"] {
  background: #ccc;
}

.modal-content .close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  background: none;
  border: none;
  color: #aaa;
  cursor: pointer;
}
